import jsPDF from "jspdf";
import "jspdf-autotable";
import { formatGram, formatRp, logo_ayu_base64 } from "../../helper";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const LaporanPenjualanCustomer = (data, mulai_dari, sampai_dengan) => {
  console.log(data);
  // initialize jsPDF
  const doc = new jsPDF("portrait");
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  let finalY = 30;
  doc.setFontSize(15);
  doc.text("LAPORAN PENJUALAN CUSTOMER", 14, 33);
  doc.addImage(logo_ayu_base64, "PNG", 14, 10, 35, 12);
  doc.setFontSize(10);
  doc.text(`Mulai Dari : ${mulai_dari}`, 14, 39);
  //   row 2
  doc.text(`Sampai Dengan	: ${sampai_dengan}`, 100, 39);
  //   //row 1
  //   doc.text(`Supplier : ${row1isi}`, 14, 25);
  //   //   row 2
  //   doc.text(`Tanggal	: ${row2isi}`, 120, 25);
  data.forEach((item, index) => {
    let tableColumn = [
      [
        {
          content: `TANGGAL : ${item.tanggal}`,
          colSpan: 2,
        },
        {
          content: `NO BON : ${item.no_bon}`,
          colSpan: 3,
        },
        {
          content: `NAMA TOKO : ${item.nama_toko}`,
          colSpan: 3,
        },
      ],
      [
        {
          content: `ONGKOS : ${formatRp(item.ongkos_rp)}`,
          colSpan: 2,
        },
        {
          content: `DISC GLOBAL (GR) : ${formatGram(item.dics_global)}`,
          colSpan: 2,
        },
        {
          content: `TOTAL NETTO (GR) : ${formatGram(item.total_netto)}`,
          colSpan: 2,
        },
        {
          content: `TOTAL NETTO EXT (GR) : ${formatGram(item.total_netto_ext)}`,
          colSpan: 2,
        },
      ],
      [
        {
          content: `KODE JENIS`,
        },
        {
          content: `NAMA JENIS`,
        },
        {
          content: `CAP KODE`,
          styles: { halign: "right", cellWidth: 16 },
        },
        {
          content: `KODE TUKAR`,
          styles: { halign: "right", cellWidth: 16 },
        },
        {
          content: `BRUTO (GR)`,
          styles: { halign: "right" },
        },
        {
          content: `NETTO (GR)`,
          styles: { halign: "right" },
        },
        {
          content: `DISC (GR)`,
          styles: { halign: "right" },
        },
        {
          content: `NETTO DISC (GR)`,
          styles: { halign: "right" },
        },
      ],
    ];
    item.detail_barang.forEach((hasil) => {
      let rows = [
        hasil.kode_jenis,
        hasil.nama_jenis,
        { content: hasil.kadar_cetak, styles: { halign: "right" } },
        { content: hasil.kadar_jual, styles: { halign: "right" } },
        { content: formatGram(hasil.bruto), styles: { halign: "right" } },
        { content: formatGram(hasil.netto, 3), styles: { halign: "right" } },
        { content: formatGram(hasil.disc), styles: { halign: "right" } },
        {
          content: formatGram(hasil.netto_disc, 3),
          styles: { halign: "right" },
        },
      ];
      tableRows.push(rows);
    });
    doc.autoTable({
      head: tableColumn,
      body: tableRows,
      startY: index === 0 ? 45 : finalY + 10,
      theme: "plain",
      rowPageBreak: "avoid",
      margin: { top: 20 },
      bodyStyles: { lineColor: "#969696", fontSize: 8, lineWidth: 0.05 },
      headStyles: {
        lineColor: "#969696",
        lineWidth: 0.05,
        fontSize: 8,
        fillColor: "#E8E8E8",
      },
      footStyles: {
        lineColor: "#969696",
        lineWidth: 0.05,
        fillColor: "#E8E8E8",
        fontSize: 8,
      },
    });
    finalY = doc.autoTableEndPosY() + 10;
    tableRows = [];
  });

  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  doc.autoPrint();
  doc.setProperties({
    title: "LAPORAN PENJUALAN CUSTOMER",
  });
  // doc.save(`PenerimaanSUpplier.pdf`);
  var string = doc.output("datauristring");
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>LAPORAN PENJUALAN CUSTOMER</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default LaporanPenjualanCustomer;
